export default {
	name: "privacy",
	props: {
		// headerImg:{
		// 	type:String,
		// 	default:'',
		// },
	},
	data() {
		return {
			privacyContractName: '',
			showPrivacy: false,
		}
	},
	created() {
		console.log('弹窗1');
		uni.getPrivacySetting({
			success:res=> {
				console.log(res,'弹窗2');
				if (res.errMsg == "getPrivacySetting:ok") {
					this.privacyContractName = res.privacyContractName;
					this.showPrivacy = res.needAuthorization;
				}
			}
		})
	},
	methods: {
		// 打开隐私协议页面
		openPrivacyContract() {
			const _ = this
			uni.openPrivacyContract({
				fail: () => {
					uni.showToast({
						title: '遇到错误',
						icon: 'error'
					})
				}
			})
		},
		// 拒绝隐私协议
		exitMiniProgram() {
			// 直接退出小程序
			uni.exitMiniProgram()
			// uni.navigateBack({
			// 	delta: 1
			// });
		},
		// 同意隐私协议
		handleAgreePrivacyAuthorization() {
			this.showPrivacy = false;
		},
	}
}
